import { Component, Prop, h, Host } from '@stencil/core';
import { classlist } from '../../../utils/classlist';

const variantsConfig = {
    variants: {
        layout: {
            horizontal: 'inline-block justify-center',
            vertical: 'flex justify-start',
        },
        active: {
            true: 'z-index-20 text-active-foreground focus:outline-2 focusfocus-visible:ring-active-foreground/30',
            false: 'text-light hover:bg-surface-1 focus-visible:ring-active-foreground/30',
        },
    },
    compoundVariants: [
        {
            layout: 'vertical',
            active: true,
            class: 'bg-active-foreground/10',
        },
    ],
};

export type Layout = 'vertical' | 'horizontal';

/**
 * @category Navigation
 * @description A tab trigger component for the tab selector.
 * @slot - Use this slot to add custom elements after the tab title.
 */

@Component({
    tag: 'at-tab-trigger',
    shadow: false,
})
export class AtTabTrigger {
    /**
     * ID of the tab
     */
    @Prop()
    tab_id: string;

    /**
     * Title to be displayed in the tab
     */
    @Prop()
    tab_title: string;

    /**
     * Styling based on the layout of the tabs
     */
    @Prop()
    layout: Layout = 'horizontal';

    /**
     * Applies styling when active
     */
    @Prop()
    is_active: boolean;
    /**
     * If true, the tab will fill the parent container's width.
     * Set by parent <at-tabs> to control layout.
     */
    @Prop()
    fill: boolean = false;

    tabEl: HTMLElement;

    handleKeyDown(event: KeyboardEvent) {
        if (event.key === 'Enter' || event.key === ' ') {
            this.tabEl.click();
        }
    }

    render() {
        const getClassname = classlist(
            'group/tab text-button relative box-border flex cursor-pointer items-center gap-4 overflow-hidden rounded-md border border-solid border-transparent font-medium capitalize duration-150 ease-in-out p-8 outline-none focus-visible:ring-active-foreground/40 focus-visible:ring-2 focus-visible:z-20',
            variantsConfig,
        );

        const classname = getClassname({
            layout: this.layout,
            active: this.is_active,
        });

        return (
            <Host
                role="tab"
                tabfocs={0}
                aria-selected={this.is_active}
                data-active={this.is_active ? 'true' : 'false'}
                onKeyDown={(event) => this.handleKeyDown(event)}
                data-name={`tab-${this.tab_id}`}
                class={this.fill ? 'flex-1' : undefined}
            >
                <a
                    id={`tab-${this.tab_id}`}
                    tabindex="0"
                    class={classname}
                    ref={(el) => (this.tabEl = el)}
                >
                    {this.tab_title}
                    <slot />
                </a>
            </Host>
        );
    }
}
